h1, h2, h3, h4, h5 {
  font-family: 'Patua One', cursive;
  font-weight: normal; }

body {
  background: url("/wedding/assets/images/IMG_5440.jpg") no-repeat;
  background-size: cover;
  font-family: Roboto, sans-serif;
  text-align: center; }

p {
  font-size: 16px; }

.container {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 60em;
  padding: 1em;
  width: 100%; }

.main-container {
  background: rgba(255, 255, 255, 0.7);
  margin-top: 7em; }

.page_title {
  margin-top: 0;
  padding-top: .2em; }

.cursive {
  font-family: 'Satisfy', cursive;
  font-size: 3em;
  margin: 0 auto .7em; }

.mdl-button {
  font-size: 12px; }

.rsvp-form {
  margin-bottom: 2em; }

.form-actions {
  padding: 2em 0; }

.spacer {
  padding: .5em; }

.helper-text {
  font-size: 2em;
  margin-bottom: 1em; }

.message {
  background: #222;
  color: #fafafa;
  margin-bottom: 1em;
  padding: 1em;
  font-style: italic; }

img.full-width {
  height: auto;
  width: 100%; }

.result-table {
  width: 100%; }

.result-name {
  width: 200px; }

.result-side {
  width: 100px; }

